/* Gallery */
.port {
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 0;
  /* min-width: 320px;
  max-width: 410px; */
  width: 100%;
  text-align: center;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  box-sizing: border-box;
}

.port * {
  /* transition: all 0.35s ease-in-out; */
  color: #fff;
  text-decoration: none;
  font-size: 16px;
}

.port:hover {
  cursor: pointer;
  transform: scale(.9);
  background: #33a59c;
  opacity: 0.8;
  color: #fff;
  transition: all 2s;
}

.port *:hover {
  color: #fff;
  /* transform: rotate(360deg);
  transition: all  1s; */

}


img.project-img {
  max-width: 100%;
  vertical-align: middle;
  padding: 10% 0%;
}

.port:hover img {
  opacity: 0;
  transform: rotate(360deg);
  transition: all 3s;
}

.text {
  width: 80%;
  height: 90%;
  position: absolute;
  top: -100px;
  left: 10%;
  color: #2a66dd;
}

.text h3 {
  color: black;
}

.port:hover .text {
  top: 20%;
}

.port:hover .button {
  bottom: 40%;
}

.port .button {
  position: absolute;
  bottom: -100px;
  left: 25%;
  width: 50%;
  border: 3px solid #fff;
  padding: 15px;
  box-sizing: border-box;
  transition: all 0.3s ease-in-out;
}

.button:hover {
  background-color: #000;
  color: #fff;
  border: 3px solid #000
}

.box {
  padding: 60px 0px;
}

.box-part {
  background: #F0FFFF;

  padding: 60px 10px;
  margin: 30px 0px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-bottom: 25px;
}

.box-part:hover {
  background: #000000;
}

.box-part:hover .fa,
.box-part:hover .title,
.box-part:hover .text,
.box-part:hover a {
  color: #FFF;
  -webkit-transition: all 1s ease-out;
  -moz-transition: all 1s ease-out;
  -o-transition: all 1s ease-out;
  transition: all 1s ease-out;
}

.text {
  margin: 0px 0px;
}

.fa {
  color: #00BFFF;
}

/* clock */
.clock {
  color: #fff;
  letter-spacing: 7px;
  text-align: center;
}

.frame {
  display: inline-block;
  vertical-align: middle;
  margin-top: 20px;
  padding: 10px 10px 10px 30px;
  border: 10px solid #000;
  background-color: #eaeaea;
  position: relative;
  transition: .3s ease;
  box-shadow: inset 1px 1px 3px rgba(0, 0, 0, .3),
    1px 1px 2px rgba(0, 0, 0, .2),
    40px 35px 70px -10px rgba(0, 0, 0, .5),
    -20px -20px 100px -10px rgba(255, 255, 255, .8);
}

a.btn.btn-light.btn-lg.section-scroll {
  border: 2px solid #000;
  /* background: transparent; */
}

a.btn.btn-light.btn-lg.section-scroll:hover {
  background: #000;
  border: 2px soild #fff !important;
  color: #fff;
}

.fading {
  animation-name: fade-in;
  animation-duration: 7s;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}


/* background gradient */
.gradient_background {
  background: linear-gradient(to right,
      #6f7280,
      #2a2c3c,
      #181827,
      #2a66dd,
      #fd1d1d,
      #fcb045);
  background-size: 400% 400%;
  animation: body 10s infinite ease-in-out;
}

@keyframes body {
  0% {
    background-position: 0 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0 50%;
  }
}


